@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
body {
  margin: 0;
  font-family: 'Big Shoulders Text', sans-serif;
}

h1, h2, h3, h4, h5, p, a {
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-size: 1.7rem;
  font-weight: 900;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.route-wrapper {
  position: absolute;
}

.route-wrapper > div {
  position: absolute;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.wrapperHome {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.wrapperHome .row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin: 0;
}

.wrapperHome .column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  background: #000000;

}

.wrapperHome .column.contenido {
  flex: 12 1;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.wrapperHome .blue-column {
  height: 100vh;
  display: flex;
  align-items: center;
  background: #111;
  justify-content: center;
  box-shadow: 0 0 10px inset #000000;
}

.wrapperHome .blue-column i.material-icons {
  font-size: 2em;
  color: #ffffff;
  margin: 20px 0;
  font-size: 1.2em;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.wrapperHome .blue-column i.material-icons:hover {
  color: #ff0000;
}

.wrapperHome .green-column {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
  color: #333;
}

.wrapperHome .green-column a {
  color: #000000;
}

.menu {
    width: 100%;
    background: #2d3e50;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.menu .datos.personales h1 {
    font-size: 1.4rem;
    font-weight: 900;
    padding: 10px 0;
    padding-bottom: 0;
}

.menu .datos.personales h2 {
    font-size: 1rem;
    font-weight: 200;
    padding: 2px 0;
    color: #a3aeba;
    padding-bottom: 0;
    border-bottom: 1px solid #2d3e50;
}

.menu .datos.personales img {
    border-radius: 50%;
    max-width: 80px;
    width: 80px;
    box-shadow: 0 0 10px #000000;
}

.menu .flex-column.nav {
    text-align: left;
    margin: 10px 0;
    width: 100%;
}

.menu .flex-column.nav a {
    font-size: 0.9rem;
    color: #ffffff;
    margin: 0;
    padding: 0;
    background: #29333e;
    padding: 10px;
    padding-left: 20px;
    margin-bottom: 2px;
    -webkit-transition: all 1s;
    transition: all 1s;;
}

.menu .flex-column.nav a:hover {
    background: #566e86;
}

.menu .flex-column.nav a.activo {
    background-color: gold;
}

.menu p {
    padding: 0 10px;
    font-size: 0.9rem;
    padding-left: 20px;
}

i.material-icons {
    position: relative;
    float: left;
    border: 1px solid #000;
    padding: 4px;
    margin-right: 5px;
    margin-top: -5px;
    border-radius: 50%;
}
.ps h1 {
    text-align: left;
    color: #333333;
    font-weight: 900;
    padding: 10px 0;
}

.ps p {
    text-align: left;
    color: #111;
    font-weight: 100;
    padding: 10px 0;
    font-size: 1rem;
    text-align: justify;
}
